import React from "react";
import { Posting } from "../types/job";
import AboutShow from "./AboutShow";
import Catagories from "./catagories";
type Deatil={
    job:Posting
}
type about = {
    posted_on: Date;
    deadline: Date;
    location: string;
    start_date: string;
    end_date: string;
};

type AboutShowProps = {
    data: about;
};

export function ShowDetails({job}:Deatil){
    return (
        <div className="flex justify-between">
            <div>

            </div>

            <div className="">
            <div className="mb-[20px]">
                {/*about*/}
            <p className="font-Poppins font-[600] text-[24px] leading-[1.2] ">About</p>
            <AboutShow data={job.about}/>
            </div>
            <div className="mb-[20px]">
                 <p className="font-Poppins font-[600] text-[24px] leading-[1.2] ">Catagories</p>
                 <div>

                 </div>
                   
                    
            </div>
            </div>
        </div>
    )
}