import React from 'react'
import Image from 'next/image'
type about = {
    posted_on: Date;
    deadline: Date;
    location: string;
    start_date: string;
    end_date: string;
};

type AboutShowProps = {
    data: about;
};

function AboutShow({ data }: AboutShowProps) {
  return (
    <div className='font-Epilogue text-[16px] leading-[1.6] gap-[20px]'>
      <div className='w-[142px] h-[52px] p-[10px]'>
        <div className='font-[400] text-[#515B6F]'>Posted On</div>
        <div className='font-[600] text-[#25324B]'> {new Date(data.posted_on).toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
              })}</div>
      </div>
      <div className='w-[142px] h-[52px] p-[10px]'>
        <div className='font-[400] text-[#515B6F]'>Deadline</div>
        <div className='font-[600] text-[#25324B]'> {new Date(data.deadline).toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
              })}</div>
      </div>
      <div className='w-[142px] h-[52px] p-[10px]'>
        <div className='font-[400] text-[#515B6F]'>Location</div>
        <div className='font-[600] text-[#25324B]'> {data.location}</div>
      </div>
      <div className='w-[142px] h-[52px] p-[10px]'>
        <div className='font-[400] text-[#515B6F]'>Start Date</div>
        <div className='font-[600] text-[#25324B]'> {new Date(data.start_date).toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
              })}</div>
      </div>
      <div className='w-[142px] h-[52px] p-[8px]'>
        <div className='font-[400] text-[#515B6F]'>End Date</div>
        <div className='font-[600] text-[#25324B]'> {new Date(data.end_date).toLocaleDateString('en-US', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
              })}</div>
      </div>
    </div>
  )
}

export default AboutShow
